import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import baseUrl from 'src/app/_helper';

@Injectable({
  providedIn: 'root'
})
export class ShiftService {

  constructor(private http:HttpClient) { }

  public createShift (shift){
    return this.http.post(`${baseUrl}/shft/save`,shift);
  }
  public getAllShift (){
    return this.http.get(`${baseUrl}/shft/findAll`);
  }

  public getAllActiveAssignShift()
  {
    return this.http.get(`${baseUrl}/shftAssign/findAllActive`);
  }
  public assignShift(value)
  {
    return this.http.post(`${baseUrl}/shftAssign/save`,value);
  }

}
